document.addEventListener('DOMContentLoaded', function(){
    fixedNav();
    galleria();
})



function fixedNav(){
    
    const barra = document.querySelector('.navegacion');
    const sobreFestival = document.querySelector('#main_container');
    const body = document.querySelector('body')
    
    if (window.innerWidth > 768) {
        window.addEventListener('scroll', () => {
            if (sobreFestival.getBoundingClientRect().top < 0) {
                barra.classList.add('fijo');
                body.classList.add('body-scroll');
            } else {
                barra.classList.remove('fijo');
                body.classList.remove('body-scroll');
            }
        });
    }
    
}



function galleria() {
    const galeria = document.querySelector('#galeria'); // Corregido: Selecciona por clase '.galeria'
    galeria.classList.add('artwork-grid')
    const btn = document.querySelector('#galleryBTN')

    for(let id = 1; id <= 17; id++){
        let imgPath = `public/build/img/imagen (${id})`;

        let box = document.createElement('picture');
        box.classList.add('box')
        box.innerHTML = `
        <source srcset="${imgPath}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="${imgPath}.png" alt="Imagen galeria">
        `
        btn.onclick = function(){
            mostrarImagen(1);
            scrollToTop()
        }
        galeria.appendChild(box)
    }


}


function mostrarImagen(id){
    const imagen = document.createElement('picture');
    let imgPath = `public/build/img/imagen (${id})`;
    imagen.innerHTML = `
    <source srcset="${imgPath}.webp" type="image/webp">
    <img loading="lazy" width="200" height="300" src="${imgPath}.png" alt="Imagen galeria">
    `
    const body = document.querySelector('body')

    const contenedor_botones = document.createElement('DIV');
    contenedor_botones.classList.add('btn-container');

    const right = document.createElement('P');
    const left  = document.createElement('P');
    right.classList.add('btn');
    left.classList.add('btn');
    right.innerHTML = `
    <i class="fa-solid fa-arrow-right"></i>`
    left.innerHTML = `
    <i class="fa-solid fa-arrow-left"></i>`

    contenedor_botones.appendChild(right);
    contenedor_botones.appendChild(left);


    //crea overlay
    const overlay = document.createElement('DIV');

    body.appendChild(contenedor_botones);

    desplazar(id)

    overlay.classList.add('overlay');
    overlay.appendChild(imagen)
    body.appendChild(overlay)
    body.classList.add('fijar-body')



    overlay.addEventListener('click',()=>{
        removerOverlay()
    })
   
    }


    function desplazar(id){
        const right = document.querySelector('.fa-arrow-right')
        const left = document.querySelector('.fa-arrow-left')

        right.addEventListener('click',()=>{
            
            id+=1
            if(id > 17){
                id = 1;
                removerOverlay();
                mostrarImagen(id);
            }
            removerOverlay();
            scrollToTop()
            mostrarImagen(id);

            removerOverlay();
            scrollToTop()
            mostrarImagen(id);

        })

        left.addEventListener('click',()=>{
            
            id-=1 
            if(id < 1){
                id = 17;
                removerOverlay();
                mostrarImagen(id);
            }
            removerOverlay();
            scrollToTop()
            mostrarImagen(id);

        })
    }

    function removerOverlay(){

        document.body.classList.remove('fijar-body')
        document.querySelector('.overlay').remove()
        document.querySelector('.btn-container').remove()
    }

    function scrollToTop() {
        window.scrollTo({
            top: 0,
            behavior: 'smooth' // Opcional: para un desplazamiento suave
        });
    }
